<template>
    <div class="registerUser">
        <form @submit.prevent="register" class="registration-form home-page">
            <h4 class="text-center">Registration</h4>
            <div class="form-group outlinedInput">
                <label class="form-label" for="emailInput">Email</label>
                <input type="text" id="emailInput" v-model="email" name="email" class="form-control">
                <div class="invalid-feedback">asdasd</div>
            </div>
            <div class="form-group outlinedInput">
                <label class="form-label" for="userEmail">Senha</label>
                <input class="form-control"  type="password" id="userPassword" v-model="password" name="email">
                <div class="invalid-feedback">asdasd</div>
            </div>

            <div>
                <button class="btn btn-primary" type="submit">
                    Submit
                </button>
            </div>

            <div>
                {{ error }}
            </div>
        </form>

    </div>
</template>

<script>
import AuthenticationService  from '@/services/AuthenticationService';

export default { 
    name: 'RegisterUser', 
    data(){ 
        return { 
            email: '',
            password: '', 
            error: null
        }
    },

    methods: { 
        async register() { 
            try {                
                await AuthenticationService.register({ 
                    email: this.email,
                    password: this.password
                }) 
            } catch (error) {
                console.log(error);
                this.error = error.response.data.msg

            }
        }, 
        
    }
}
</script>


<style lang="scss">
.registration-form { 
    min-width: 320px;
}
</style>